
.star{
  font-size: 0;
}
.star-item{
    display: inline-block;
    background-repeat: no-repeat;
}
.star-48>.star-item{ 
    width: 20px;
    height: 20px;
    margin-right: 22px;
    background-size: 20px 20px;
}
.star-48>.on{
  background-image: url('star48_on@3x.png');
}
.star-48>.half{
  background-image: url('star48_half@3x.png');
}
.star-48>.off{
  background-image: url('star48_off@3x.png');
}
.star-36>.star-item{ 
    width: 15px;
    height: 15px;
    margin-right: 6px;
    background-size: 15px 15px;
}
.star-36>.on{
  background-image: url('star48_on@3x.png');
}
.star-36>.half{
  background-image: url('star48_half@3x.png');
}
.star-36>.off{
  background-image: url('star48_off@3x.png');
}
.star-24>.star-item{ 
    width: 10px;
    height: 10px;
    margin-right: 3px;
    background-size: 10px 10px;
}
.star-24>.on{
  background-image: url('star48_on@3x.png');
}
.star-24>.half{
  background-image: url('star48_half@3x.png');
}
.star-24>.off{
  background-image: url('star48_off@3x.png');
}




