
* {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
.picture {
  position: relative;
  background: #333;
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 0;
}
.picture .photo_wrap {
  top: 50%;
  left: 50%;
  width: 180px;
  height: 260px;
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
  position: absolute;
  margin-top: -130px;
  margin-left: -90px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.picture .photo_wrap.active {
  top: 50% !important;
  left: 50% !important;
  -webkit-transform: rotate(360deg) !important;
          transform: rotate(360deg) !important;
  width: 240px;
  height: 300px;
  position: absolute;
  margin-top: -150px;
  margin-left: -120px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  z-index: 100;
}
.picture .photo_wrap .photo_content {
  -webkit-transform-origin: 0% 100%;
          transform-origin: 0% 100%;
  -webkit-perspective: 800px;
          perspective: 800px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.picture .photo_wrap .photo_content.front_active {
  -webkit-transform: translate(180px, 0px) rotateY(180deg);
          transform: translate(180px, 0px) rotateY(180deg);
}
.picture .photo_wrap .photo_content.back_active {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}
.picture .photo_wrap .photo_content .photo {
  position: absolute;
}
.picture .photo_wrap .photo_content .photo_front {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}
.picture .photo_wrap .photo_content .photo_back {
  font-size: 15px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  background: #ffc0cb;
}
.picture .icon_img {
  width: 100%;
  text-align: center;
  height: 100px;
  bottom: 20px;
  display: block;
  position: fixed;
}
.picture .icon_img .icon_item {
  display: inline-block;
  margin-left: 10px;
  background: #fff;
  height: 15px;
  width: 15px;
  border-radius: 10px;
}
.picture .icon_img .icon_item.active {
  background: #fff;
  padding: 2px;
  border-radius: 5px;
  height: 20px;
  width: 20px;
}
.picture .icon_img .icon_item.rot_active {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.picture .icon_img .icon_item .icon_content {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.picture .icon_img .icon_item .icon_content.front_active {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.picture .icon_img .icon_item .icon_content.back_active {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}


